<template>
  <div class="row" v-if="isReady">
    <div class="col-12 flex-x-between mt-2">
      <div class="flex-align-center">
        <h3 class="my-0">Finance</h3>
      </div>

      <div class="flex-align-center flex-end">
        <el-button
          type="primary"
          class="mx-0"
          @click="$router.push('/Expense/add-new-finance')"
        >
          Add Expense
        </el-button>
        <el-button
          type="primary"
          class="mx-2"
          @click="$router.push('/Incomes/add-new-finance')"
        >
          Add Income
        </el-button>
        <el-date-picker
          v-model="month"
          @change="getData"
          class="white-bg"
          format="MM/yyyy"
          value-format="MM/yyyy"
          type="month"
          placeholder="Pick a month"
        >
        </el-date-picker>
      </div>
    </div>
    <div class="col-12 mt-3">
      <div class="row finance">
        <div class="col-lg-4 pr-lg-2">
          <div
            class="box balance animated fadeInRight position-relative"
            style="animation-delay: 200ms"
          >
            <svg
              viewBox="0 0 800 400"
              xmlns="http://www.w3.org/2000/svg"
              xmlns:xlink="http://www.w3.org/1999/xlink"
            >
              <path
                id="curve"
                fill="rgba(255,255,255,0.2)"
                d="M 800 300 Q 400 207.06666564941406 0 300 L 0 0 L 800 0 L 800 300 Z"
              ></path>
            </svg>
            <h3 class="mt-2 text-white">Main Balance</h3>
            <el-button
              v-ripple.400
              type="white"
              circle
              class="no-width edit-balance flat"
              @click="openDialog = !openDialog"
            >
              <i class="bx bxs-edit-alt text-white"></i>
            </el-button>

            <p class="mb-0 text-white">Total main balance</p>
            <p class="mb-0 mt-5 text-white">
              <span style="font-size: 30px !important">{{ balance }}</span>
              <span class="title"> MMK </span>
            </p>
          </div>
        </div>
        <div class="col-lg-4 px-lg-2">
          <div
            class="box expense animated fadeInRight"
            style="animation-delay: 300ms"
          >
            <svg
              viewBox="0 0 800 400"
              xmlns="http://www.w3.org/2000/svg"
              xmlns:xlink="http://www.w3.org/1999/xlink"
            >
              <path
                id="curve"
                fill="rgba(255,255,255,0.2)"
                d="M 800 300 Q 400 207.06666564941406 0 300 L 0 0 L 800 0 L 800 300 Z"
              ></path>
            </svg>
            <h3 class="mt-2 text-white">Expenses</h3>
            <p class="mb-0 text-white">
              Total expenses on
              {{ $moment(month, "MM/YYYY").format("MMMM, YYYY") }}
            </p>
            <p class="mb-0 mt-5 text-white">
              <span style="font-size: 30px !important">{{
                expense.price
              }}</span>
              <span class="title"> MMK </span>
            </p>
          </div>
        </div>
        <div class="col-lg-4 pl-lg-2">
          <div
            class="box incomes animated fadeInRight"
            style="animation-delay: 400ms"
          >
            <svg
              viewBox="0 0 800 400"
              xmlns="http://www.w3.org/2000/svg"
              xmlns:xlink="http://www.w3.org/1999/xlink"
            >
              <path
                id="curve"
                fill="rgba(255,255,255,0.2)"
                d="M 800 300 Q 400 207.06666564941406 0 300 L 0 0 L 800 0 L 800 300 Z"
              ></path>
            </svg>
            <h3 class="mt-2 text-white">Incomes</h3>
            <p class="mb-0 text-white">
              Total incomes on
              {{ $moment(month, "MM/YYYY").format("MMMM, YYYY") }}.
            </p>
            <p class="mb-0 mt-5 text-white">
              <span style="font-size: 30px !important">{{
                incomes.price
              }}</span>
              <span class="title"> MMK </span>
            </p>
          </div>
        </div>
      </div>
    </div>
    <div class="col-12 list-table mt-3">
      <div class="box pt-3">
        <div class="row">
          <div class="col-12 flex-align-center">
            <p
              class="mx-0 table-tab"
              :class="filters.type === '' && 'active-table-tab'"
              @click="filters.type = ''"
            >
              All
            </p>
            <p
              class="mx-0 table-tab"
              :class="filters.type === 'Expense' && 'active-table-tab'"
              @click="filters.type = 'Expense'"
            >
              Expense
            </p>
            <p
              :class="filters.type === 'Incomes' && 'active-table-tab'"
              class="mx-0 table-tab"
              @click="filters.type = 'Incomes'"
            >
              Incomes
            </p>
          </div>
          <div class="col-12"><div class="b-divider mb-1 mt-0"></div></div>
          <div
            class="
              col-12
              mb-1
              d-flex
              justify-content-between
              align-items-center
            "
          >
            <div class="flex-align-center">
              <el-select
                v-model="filters.pageSize"
                class="mr-2 page-size-selector mr-2"
              >
                <el-option
                  v-for="page in 10"
                  :key="page"
                  :label="page * 5"
                  :value="page * 5"
                >
                </el-option>
              </el-select>
              <el-input
                v-model="filters.query"
                class="mr-2 table-search"
                placeholder="Type something"
                prefix-icon="bx bx-search"
              >
              </el-input>
              <el-select
                v-model="filters.label"
                filterable
                clearable
                class="mr-2 filter-input"
                placeholder="Label"
              >
                <el-option
                  v-for="label in labels"
                  :key="label.id"
                  :label="label.name"
                  :value="label.name"
                >
                </el-option>
              </el-select>
              <el-date-picker
                class="mr-2 filter-input"
                v-model="filters.date"
                type="date"
                format="dd/MM/yyyy"
                value-format="dd/MM/yyyy"
                placeholder="Pick a date"
              >
              </el-date-picker>
            </div>

            <el-popover placement="bottom" width="200" trigger="click">
              <p class="font-weight-bold">Sort By</p>
              <div class="b-divider my-2"></div>
              <div class="w-100">
                <el-radio
                  v-model="filters.order_by"
                  v-for="(sort, n) in sortList"
                  :key="sort.name"
                  :class="n !== sortList.length - 1 && 'mb-3'"
                  :label="sort.value"
                >
                  {{ sort.name }}
                </el-radio>
              </div>
              <el-button
                class="mr-0 flat no-width"
                v-ripple.400="'rgba(0,0,0,0.2)'"
                slot="reference"
                type="dark"
              >
                <i class="bx bx-sort mr-1"></i> Sort
              </el-button>
            </el-popover>
          </div>
        </div>
        <div class="w-100 el-main-table" v-loading="loading">
          <div class="el-table-header" v-if="selected.length > 0">
            <div class="d-flex align-items-center">
              <el-checkbox
                :indeterminate="selected.length == finances.results.length"
                v-model="allCheck"
                @change="$refs.multipleTable.toggleAllSelection"
                >{{ selected.length }} selected</el-checkbox
              >
              <el-dropdown @command="changeAction" trigger="click">
                <p
                  class="el-dropdown-link ml-3 pointer primary hover-underline"
                >
                  Actions
                  <i class="el-icon-arrow-down el-icon--right"></i>
                </p>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item
                    v-for="action in actions"
                    :key="action.value"
                    :command="action.value"
                  >
                    {{ action.label }}</el-dropdown-item
                  >
                </el-dropdown-menu>
              </el-dropdown>
            </div>
          </div>
          <el-table
            ref="multipleTable"
            :data="finances.results"
            style="width: 100%"
            @row-click="
              (row) =>
                $router.push({
                  name: 'Edit Finance',
                  params: { id: row.id },
                })
            "
            @selection-change="handleSelectionChange"
          >
            <el-table-column type="selection"> </el-table-column>
            <el-table-column label="Finance" width="150">
              <template slot-scope="scope">
                <el-tag
                  type="success"
                  class="rounded"
                  v-if="scope.row.type == 'Incomes'"
                  ><i class="bx bx-down-arrow-circle"></i>
                  {{ scope.row.type }}</el-tag
                >
                <el-tag
                  type="danger"
                  class="rounded"
                  v-if="scope.row.type == 'Expense'"
                  ><i class="bx bx-up-arrow-circle"></i
                  >{{ scope.row.type }}</el-tag
                >
              </template>
            </el-table-column>

            <el-table-column property="label_name" label="Label">
            </el-table-column>
            <el-table-column property="note" label="Note"> </el-table-column>
            <el-table-column property="date" label="Date"> </el-table-column>
            <el-table-column property="amount" label="Amount">
              <template slot-scope="scope">
                <p>{{ scope.row.amount }} MMK</p>
              </template>
            </el-table-column>
            <template #empty>
              <el-empty description="No data found!">
                <template #image> <empty /> </template
              ></el-empty>
            </template>
          </el-table>
          <div class="el-table-footer" v-if="finances.total_pages !== 1">
            <el-pagination
              background
              @current-change="
                (index) => ((finances.current_page = index), getFinances())
              "
              :current-page="finances.current_page"
              layout="prev, pager, next"
              :page-count="finances.total_pages"
            >
            </el-pagination>
          </div>
        </div>
      </div>
    </div>

    <el-dialog
      class="default-dialog"
      :show-close="false"
      :visible.sync="openDialog"
    >
      <template #title>
        <div class="d-flex align-items-center justify-content-between">
          <h5 class="my-0">Main Balance</h5>
          <div class="d-flex align-items-center"></div>
        </div>
      </template>
      <div class="con-content">
        <label for="Phone" class="custom-label">Amount</label>
        <el-input
          name="Amount"
          type="number"
          class="w-100 mb-3"
          v-model="balance"
        />
      </div>

      <template #footer>
        <div class="d-flex align-items-center justify-content-end">
          <el-button
            @click="openDialog = false"
            class="my-0 flat mr-0"
            type="primary"
            v-ripple.400="'rgba(var(--primary),0.2)'"
          >
            Cancel
          </el-button>
          <el-button
            class="my-0 mr-0"
            v-ripple.400
            type="primary"
            :loading="posting"
            @click="Post"
          >
            Save
          </el-button>
        </div>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { empty } from "../../common";
export default {
  data() {
    return {
      openDialog: false,
      posting: false,
      allCheck: false,
      search: "",
      page: 1,
      max: 5,
      active: 0,
      selected: [],
      finances: {
        current_page: 1,
        results: [],
      },
      filters: {
        query: "",
        order_by: "-updated_at",
        pageSize: 10,
        label: "",
        type: "",
        date: "",
      },
      month: null,
      actions: [
        {
          label: "Delete item",
          value: "delete",
        },
      ],
      sortList: [
        {
          name: "Note A–Z",
          value: "note",
        },
        {
          name: "Note Z–A",
          value: "-note",
        },
        {
          name: "Created (oldest first)",
          value: "created_at",
        },
        {
          name: "Created (newest first)",
          value: "-created_at",
        },
        {
          name: "Updated (oldest first)",
          value: "updated_at",
        },
        {
          name: "Updated (newest first)",
          value: "-updated_at",
        },
      ],
      loading: true,
      labels: [],
      expense: {},
      incomes: {},
      balance: 0,
    };
  },
  computed: {
    ...mapState(["isReady", "store"]),
  },
  components: { empty },
  watch: {
    filters: {
      handler: function () {
        this.getFinances();
      },
      deep: true,
    },
  },
  created() {
    this.$store.commit("setIsReady", false);
    this.month = this.$moment().format("MM/YYYY");
    this.getData();
    this.getLabels();
    this.getFinances();
  },
  methods: {
    toggleSelection(rows) {
      if (rows) {
        rows.forEach((row) => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.selected = val;
    },
    getLabels() {
      var url = `labels`;
      this.$axios
        .get(url)
        .then((response) => {
          this.labels = response.data;
        })
        .catch((err) => {
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },
    getFinances() {
      if (this.filters.date == null) {
        this.filters.date = "";
      }
      this.loading = true;
      var url = `finances/?page=${this.finances.current_page}&query=${this.filters.query}&date=${this.filters.date}&label=${this.filters.label}&type=${this.filters.type}&order_by=${this.filters.order_by}&page_size=${this.filters.pageSize}`;
      this.$axios
        .get(url)
        .then((response) => {
          this.loading = false;
          this.finances.results = response.data.results;
          if (response.data.results[0]) {
            this.balance = response.data.results[0].balance;
          }
          this.finances.total_pages = response.data.total_pages;
          this.$store.commit("setIsReady", true);
        })
        .catch((err) => {
          this.loading = false;
          this.$noti.display({
            message: err,
            title: "",
            status: "error",
            color: "error",
          });
        });
    },
    getData() {
      this.loading = true;
      var expense_url = `finance-report/?type=Expense&month=${this.month}`;
      this.$axios
        .get(expense_url)
        .then((response) => {
          this.loading = false;
          this.balance = this.store.balance;
          this.expense = response.data[0];
        })
        .catch((err) => {
          this.loading = false;
          this.$noti.display({
            message: err,
            title: "",
            status: "error",
            color: "error",
          });
        });
      var income_url = `finance-report/?type=Incomes&month=${this.month}`;
      this.$axios
        .get(income_url)
        .then((response) => {
          this.loading = false;
          this.incomes = response.data[0];
        })
        .catch((err) => {
          this.loading = false;
          this.$noti.display({
            message: err,
            title: "",
            status: "error",
            color: "error",
          });
        });
    },
    changeAction(action) {
      if (action === "delete") {
        this.removeFinances();
      }
    },
    removeFinances() {
      if (this.selected.length > 0) {
        this.loading = true;
        var data = [];
        this.selected.forEach((Delivery) => {
          data.push(Delivery.id);
        });
        this.$axios.post("remove-finances/", { data }).then(() => {
          this.getFinances();
          this.getData();
          this.$noti.display({
            message: "Cool! Item has been successfully deleted.",
            title: "success",
            status: "normal",
            color: "success",
          });
          this.loading = false;
        });
      }
    },
    Post() {
      this.posting = true;
      this.store.balance = this.balance;
      this.$axios
        .put("store/", { data: this.store })
        .then(() => {
          this.posting = false;
          this.openDialog = false;
        })
        .catch((err) => {
          this.posting = false;
          this.$noti.display({
            message: err,
            title: "",
            status: "error",
            color: "error",
          });
        });
    },
  },
};
</script>

<style >
.el-date-editor {
  border-radius: var(--input-border-radius) !important;
}
.white-bg input.el-input__inner {
  background: rgb(var(--background-1));
}
.edit-balance {
  position: absolute !important;
  right: 15px;
  top: 15px;
}
.finance svg {
  position: absolute;
  bottom: -40px;
  right: 0px;
  left: 0px;
  transform: rotate(180deg);
}
.finance .svg-helper {
  width: 100%;
  height: 50px;
  background: rgba(255, 255, 255, 0.2);
}
.balance {
  background: #7f7fd5; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to left,
    #91eae4,
    #86a8e7,
    #7f7fd5
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to left,
    #91eae4,
    #86a8e7,
    #7f7fd5
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.expense {
  background: #4e54c8; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #8f94fb,
    #4e54c8
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #8f94fb,
    #4e54c8
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.incomes {
  background: #b993d6; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #8ca6db,
    #b993d6
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #8ca6db,
    #b993d6
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
</style>